/************************/
/*HEADER */
/***********************/
.header{
  display:flex;
  align-items: center;
  justify-content: space-between;
  height: 9.6rem;
  padding:0 4.8rem;
  position: relative;
  background-color:#000;

}

.logo {
  border-radius: 75%;
  height: 7rem;
}

.heading-secondary{
  margin-left: 5rem;
  padding: 2rem;
}
/************************/
/*NAVIGATION*/
/***********************/

.main-nav{
  position: relative;

}
.main-nav-list{
  list-style-type: none;
  display: flex;
  align-items: center;
  gap: 4.8rem;
}

.main-nav-link:link,
.main-nav-link:visited{
  display: inline-block;
  text-decoration: none;
  color: #DC143C;
  font-weight: 500;
  font-size: 1.8rem;
  transition: all 0.3s;
}

.main-nav-link:hover,
.main-nav-link:active{
  color: #fff;
}

.main-nav-link.nav-cta:link,
.main-nav-link.nav-cta:visited{
  padding: 1.2rem 2.4rem;
  border-radius: 9px;
  color: #fff;
  background-color: #DC143C;
}

.main-nav-link.nav-cta:hover,
.main-nav-link.nav-cta:active{
  background-color: #DC143C;
}

/* MOBILE NAV */
.btn-mobile-nav{
  border: none;
  background: none;
  cursor: pointer;
  display: none;
}

.icon-mobile-nav{
  height: 4.8rem;
  width: 4.8rem;
  color: #fff;
}

.icon-mobile-nav[name="close-outline"]{
  display: none;
} 

/* STICKY NAVIGATION */
.sticky .header{
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 8rem;
  padding-top: 0;
  padding-bottom: 0;
  background-color: rgba(255, 255, 255, 0.97);
  z-index: 999;
  box-shadow: 0 1.2rem 3.2rem rgba(0, 0, 0, 0.03);
}

.sticky .section-hero{
  margin-top: 9.6rem;
}

/************************/
/*HERO*/
/***********************/
.section-hero{
  padding: 0 0 0 0;
}

.hero{
  flex: 1;
  display:flex;
  justify-content: flex-end;
  background-color: #fdf2e9;
  height: 9.6rem;
  padding: 2rem 4.8rem;
  position: relative;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)), url("../assets/ingredients.jpg");
  background-repeat: no-repeat;
  object-fit: cover;
}

.hero-text-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-20%, -50%);
}

.hero-description{
  text-align: center;
  color: #fff;
  font-size: 2rem;
  line-height: 3rem;
}

/************************/
/*MEALS SECTION*/
/***********************/

.section-meals{
  padding: 9.6rem 0;
}

.meal{
  box-shadow: 0 2.4rem 4.8rem rgba(0,0,0, 0.075 );
  border-radius: 11px;
  overflow: hidden;
  transition: all 0.4s;
}

.meal:hover{
  transform: translateY(-1.2rem);
  box-shadow: 0 3.2rem 6.4rem rgba(0,0,0, 0.06 );
}

.meal-content{
  padding: 3.2rem 4.8rem 4.8rem 4.8rem;
}

.meal-tags{
  margin: 1.2rem;
  display: flex;
  gap: 0.4rem;
}

.tag{
  display: inline-block;
  padding: 0.4rem 0.8rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #333;
  border-radius: 100px;
  font-weight: 600;
}

.tag--vegetarian{
  background-color: #51cf66;
}
.tag--vegan{
  background-color: #94d82c;
}
.tag--paleo{
  background-color: #ffd43b;
}

.meal-title{
  font-size: 2.4rem;
  color: #333;
  font-weight: 600;
  margin-bottom: 3.2rem;
}

.meal-attributes{
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.meal-attribute{
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.meal-icon{
  height: 2.4rem;
  width: 2.4rem;
  color: #e67e22;
}

.meal-img{
  width: 100%;
}

.all-recipes{
  text-align: center;
  font-size: 1.8rem;
}

/************************/
/*REVIEWS*/
/***********************/
.section-reviews{
  background-color: #fdf2e9;
  padding: 4.8rem 0 9.6rem 0;
}
